// App.js
import React, { useState } from "react";

import MainHeader from "./components/MainHeader";
import SlideOutComponent from "./components/SlideOutComponent";
import WaterDrops from "./components/WaterDrops";
import HeroVideo from "./components/HeroVideo";
import AboutUs from "./components/AboutUs";
import AccordionItem from "./components/AccordianItem";
import Footer from "./components/Footer";
import faqData from "./data/faqData";
import QuiltedImageList from "./components/QuiltedImageList";
import ContactUs from "./components/ContactUs";
// import { Disclosure } from "@headlessui/react";
import { ChevronUpIcon } from "@heroicons/react/20/solid";
// import "./components/styles/styles.scss";

const styles = {
  width: "100%",
};

const App = () => {
  // create an observern  that targets  section entries   and when intersecting adds  class name of show

  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const [isAccordionVisible, setIsAccordionVisible] = useState(false);
  const toggleMenu = () => {
    setIsMenuVisible(!isMenuVisible);
  };

  return (
    <div className="app">
      <MainHeader toggleMenu={toggleMenu} />
      <div className=" relative">
        <SlideOutComponent isMenuVis={isMenuVisible} />

        <div className="">
          <HeroVideo className="snap-mandatory snap-center" />

          <div
            style={styles}
            className="relative rounded-full  py-30 snap-mandatory snap-center  "
          >
            <AboutUs />
          </div>

          <div>
            <WaterDrops />
          </div>
        </div>
        <div className="description text-white  bg-black m-6 flex p-4 self-center rounded-xl ">
          <p>
            Unlike other pool companies, we are a full-service pool company.
            Most companies only do the basic pool install and make you figure
            out where to get all the extra stuff. We take on everything from
            pool slides to waterfalls, and of course custom pool lighting! Build
            the dream pool you've always wanted with Elite Splash Pools.
          </p>
        </div>
        <div>
          <QuiltedImageList />
        </div>
        <div className="faq block my-40">
          <button
            onClick={() => setIsAccordionVisible(!isAccordionVisible)}
            className={`font-bold text-2xl p-4   flex rounded-t-lg  w-full  font-bold  text-gold  bg-black  w-full  ${
              isAccordionVisible ? "rounded-none " : "rounded-lg"
            }`}
          >
            Frequently Asked Questions
            <ChevronUpIcon
              className={`w-10 text-gold  transition-all  flex duration-500 ease-in-out text-black text-lg ${
                isAccordionVisible ? "rotate-0" : "rotate-180"
              }`}
            />
          </button>

          {faqData.map((item, index) => (
            <div className={`${isAccordionVisible ? "visible " : "hidden"}`}>
              <AccordionItem
                key={index}
                question={item.question}
                answer={item.answer}
              />
            </div>
          ))}
        </div>
        <div className="contact-us-Wrapper my-30 bg-purple-secondary pt-10">
          <ContactUs />
        </div>

        <Footer />
      </div>
    </div>
  );
};

export default App;
